
body{
  font-family: Arial, Helvetica, sans-serif;
  /* background-image: linear-gradient(to top, #00c6fb 0%, #005bea 100%); */
  background:#8C9AFE;
  height:300px;
}
.logo{
  padding:1rem;
  width:80px;
}
.info{
  /* padding:2rem; */
}
.inputs input{
  width:auto;
  font-size: 1rem;
}
select{
  font-size:1rem;
}
.legend div{
  margin:auto;
  width: 50%;
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  color:grey;
}
.bg2{
  opacity: 0.4;
  right: 0;
  top: -800px;
  width: 60vw;
  position: fixed;
  z-index: 0;
}
.bg{
  opacity:0.4;
  left: 0;
  top: -100px;
  width: 60vw;
  position: fixed;
  z-index: 0;
}
#map{
  height: 120px;
}
#map > img{
  /* border-radius: 0 0 10px 10px; */
  margin-top:1rem;
}
.inputs{
  padding:0.2rem;
}
.inputs p {
  color:grey;
}
#app{
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
}
#container{
  width:400px;
  text-align: center;
  background: white;
  -webkit-box-shadow: 0px 20px 70px #0002;
          box-shadow: 0px 20px 70px #0002;
  /* padding: 3rem 2rem 0 2rem; */
  /* border-radius: 0 0 10px 10px; */
  position: fixed;
  top:0;
}



